      <b-row class="mb-2">
        <b-card>
          <b-card-text>
            <h5>Abstract</h5>
            <p>
              Singing voice synthesis (SVS) systems have shown a remarkable ability to synthesize natural singing voices. However, existing methods have limitations in generating
              a code-mixed singing voice and still depend on a phoneme annotation in a musical score. To this end, we present X-Singer, a code-mixed SVS system by cross-lingual
              learning. First, we introduce a musical score (MS) encoder to handle a realistic musical score that comprises code-mixed lyrics without phoneme annotation. The MS
              encoder adopts language code-switching to encode the code-mixed lyrics, and a mixture alignment to reduce the dependency on the annotation. Furthermore, we adopt
              a conditional flow matching-based decoder to achieve high-quality SVS in a few sampling steps. We observe that X-Singer outperforms baseline models regarding
              naturalness on intra- and cross-lingual SVS. Moreover, our model can synthesize code-mixed SVS via cross-lingual learning on a mixture of mono-lingual SVS datasets.
            </p>
