<template>
    <b-container>
        <b-row>
            <b-col>
                <b-card 
                    v-for="(item, index) of items" :key="index"
                    class="mb-2">
                    <b-card-text>
                        <h5>{{ `Sample ${index + 1}` }}</h5>
                        <p>{{ item.text }} <br>
                           <i>  {{ item.pronunciation }} </i></p>
                    </b-card-text>
                    <b-button v-for="sample of item.audio" :key="sample.id" class="playBtn"
                    @click="play(`${title} - Sample ${index + 1}`, item.text, sample)"
                    variant="outline-primary"><font-awesome-icon icon="fa-solid fa-circle-play" /> {{sample.name}}</b-button>
                </b-card>
            </b-col>
        </b-row>
    </b-container>
</template>
<script>
export default {
    name: "SampleTable",
    props: {
        title: String,
        items: Array
    },
    methods: {
        play(title, text, sample) {
            this.$emit('playAudio', {sample: sample, title: title, text: text})
        },
    },
}
</script>
<style scoped>
    .playBtn {
        margin-right: 10px;
    }
</style>