const getters = {
    xsingerSamples(state) {
        return state.xsingerSamples;
    },
    intraaudioSamples(state) {
        return state.intraaudioSamples;
    },
    crossaudioSamples(state) {
        return state.crossaudioSamples;
    },
    ablationSamples(state) {
        return state.ablationSamples;
    }
}

export default getters;