<template>
    <b-container class="mb-3">
        <b-row>
            <b-card :title="experiment.title" class="mb-2">
                <b-card-text>
                    <b-row>
                        <b-col>
                            <audio ref="audioPlayer" preload="auto" controls></audio>
                        </b-col>
                    </b-row>
                </b-card-text>
                <b-card-text>
                    <b-row>
                        <b-col>
                            <h5>{{ play_title }}</h5>
                            <p>{{ play_text }}</p>
                        </b-col>
                    </b-row>
                </b-card-text>
                <b-row>
                    <b-col>
                        <b-tabs content-class="mt-3">
                            <b-tab v-for="(dataset, index) of experiment.datasets" 
                                :key="index" 
                                :title="dataset.title"
                                :active="dataset.title === `CNN/Daily Mail`? true : false">

                                <SampleTable 
                                    :items="dataset.samples" 
                                    :title="dataset.title" 
                                    @playAudio="handlePlayAudio" />
                            </b-tab>
                        </b-tabs>
                    </b-col>
                </b-row>
            </b-card>
        </b-row>
    </b-container>
</template>

<script>
import SampleTable from './SampleTable.vue';

export default {
    name: "SectionVue",
    components: {
        SampleTable
    },
    props: {
        experiment: Object
    },
    data() {
        return {
            play_title: "",
            play_url: "",
            play_text: "Please click the button placed on each sample to play"
        }
    },
    methods: {
        handlePlayAudio({ sample, title, text }) {
            if (sample.src) {
                this.play_title = `Playing ${title} (${sample.name})...`
                this.play_text = text
                this.$refs.audioPlayer.src = sample.src
                this.$refs.audioPlayer.play()
            }
        }
    },
}
</script>

